.library-btn__list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 65px;
  opacity: 1;
  cursor: pointer;

  &.is-hidden {
    display: none;
    opacity: 0;
  }
}

.library-btn {
  width: 130.19px;
  height: 44px;
  left: 20px;
  top: 121px;

  border: 1px solid #ffffff;
  box-sizing: border-box;
  border-radius: 5px;

  font-style: normal;
  font-weight: 500;
  line-height: 1.3;
  background-color: (#11ffee00);
  text-transform: uppercase;

  color: #ffffff;

  &:not(:last-child) {
    margin-right: 20px;
  }

  &:hover {
    color: darkgrey;
    transition: color 500ms;
    border: 1px solid #ff6b08;
    transition: border 500ms;
  }
}

.library-btn.active {
  background: #ff6b01;
  box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
  border: 0;
  color: #ffffff;
}
